header { background: rgba(15, 77, 130, 0.5); width: 100%; min-width: 320px; text-align: left; order: 1; -webkit-order: 1; position: absolute; left: 0; top: 21px; width: 100%; padding: 25px 0; z-index: 100; }

header .wrapper { display: flex; align-items: center; justify-content: space-between; max-width: 1576px; }

.logo { font-family: 'Crassula', sans-serif; color: white; font-size: 44px; font-weight: 400; text-transform: uppercase; font-weight: 500; }

.main-nav { flex: 1; padding: 0 3.8% 0 4.7%; }

.main-nav-list { display: flex; justify-content: space-between; margin: 0 -5px; }

.main-nav-list__item { padding: 5px; }

.main-nav-list__link { color: white; font-size: 23px; line-height: 28px; font-weight: 400; }

.main-nav-list__link:hover { opacity: 0.7; }

.button { font-size: 22px; font-weight: 400; line-height: 26px; height: 62px; border-radius: 37px; border: none; padding: 5px 62px; display: inline-flex; align-items: center; justify-content: center; letter-spacing: -0.2px; }

.button-white { color: #0f4d81; background: #fff; border: 2px solid #fff; }

.button-white:hover { background: transparent; color: #fff; }

.button-nav { display: none; width: 35px; height: 35px; }

.button-nav span { width: 33px; height: 3px; background: #fff; margin: 4px 0; }

.main-mask { position: fixed; right: -100%; top: 0; width: 100%; height: 100%; background: rgba(15, 77, 130, 0.3); z-index: 101; transition: all 0.3s; }

@media screen and (max-width: 1500px) { .main-nav { padding: 0 25px; } }

@media screen and (max-width: 1400px) { .button { padding: 5px 35px; }
  .main-nav-list__link { font-size: 20px; line-height: 24px; }
  .logo { font-size: 40px; } }

@media screen and (max-width: 1200px) { .logo { font-size: 36px; }
  .main-nav-list__link { font-size: 18px; line-height: 20px; }
  .button { font-size: 20px; line-height: 26px; height: 50px; }
  .main-nav-list__item { padding: 0 3px; } }

@media screen and (max-width: 1023px) { .main-nav { position: fixed; right: -280px; top: 0; height: 100%; width: 280px; background: #134268; padding: 135px 25px 25px; max-width: 100%; transition: all 0.3s; z-index: 102; }
  .main-nav-list { display: block; }
  .main-nav-list__item { padding: 0; margin: 0 0 15px 0; }
  .main-nav.show { right: 0; }
  .button-nav { display: flex; flex-direction: column; position: relative; }
  .button-nav span { transition: all 0.3s; }
  .button-nav.active { z-index: 103; }
  .button-nav.active span:first-child { transform: rotate(-45deg); position: absolute; margin: 0 0 0 0; width: 28px; left: 3px; top: 14px; }
  .button-nav.active span:nth-child(2) { background-color: transparent; }
  .button-nav.active span:nth-child(3) { transform: rotate(45deg); position: absolute; right: 3px; width: 28px; top: 14px; margin: 0 0 0 0; }
  header .button { margin: 0 35px 0 auto; }
  .main-mask.active { right: 0; }
  .noscroll { overflow: hidden; } }

@media screen and (max-width: 767px) { header { top: 0; padding: 15px 0; }
  .logo { font-size: 30px; }
  header .button { margin-right: 15px; padding: 7px 18px; font-size: 18px; line-height: 22px; height: 40px; } }

@media screen and (max-width: 470px) { header .wrapper { flex-wrap: wrap; }
  header .button { order: 3; margin: 15px auto 0; }
  .logo { font-size: 35px; } }

